import React from 'react'
// import './skills.css'
import styled from 'styled-components';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';





const MaterialUISwitch = styled(Switch)(({ theme }) => ({
    width: 62,
    height: 34,
    padding: 7,
    '& .MuiSwitch-switchBase': {
      margin: 1,
      padding: 0,
      transform: 'translateX(6px)',
      '&.Mui-checked': {
        color: '#fff',
        transform: 'translateX(22px)',
        '& .MuiSwitch-thumb:before': {
          backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
            '#fff',
          )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
        },
        '& + .MuiSwitch-track': {
          opacity: 1,
          backgroundColor: theme.palette === 'dark' ? '#8796A5' : '#aab4be',
        },
      },
    },
    '& .MuiSwitch-thumb': {
      backgroundColor: theme.palette === 'dark' ? '#003892' : '#001e3c',
      width: 32,
      height: 32,
      '&:before': {
        content: "''",
        position: 'absolute',
        width: '100%',
        height: '100%',
        left: 0,
        top: 0,
        backgroundRepeat: 'no-repeat',
        backgroundPosition: 'center',
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          '#fff',
        )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
      },
    },
    '& .MuiSwitch-track': {
      opacity: 1,
      backgroundColor: theme.palette === 'dark' ? '#8796A5' : '#aab4be',
      borderRadius: 20 / 2,
    },
  }));




function Skills() {
    function update(e){
        var x = e.clientX ;
        var y = e.clientY ;
      
        document.documentElement.style.setProperty('--cursorX', x + 'px')
        document.documentElement.style.setProperty('--cursorY', y + 'px')
      }
      
      document.addEventListener('mousemove',update)
      document.addEventListener('touchmove',update)
    


      function darkmode(){
          const Mode  = document.getElementById("mode");
          Mode.style.background="rgba(0,0,0,0)";
          window.setInterval(function darkmode(){
            Mode.style.display="none";
          },500);
          
      }
    return (
      
        <Container>
            <mode className="mode" id="mode"  > <p>Too much dark turn on the light!</p><p> Switch is at bottom right corner</p><FormControlLabel className="switch"  control={<MaterialUISwitch onClick={darkmode} sx={{ m: 1 }} defaultChecked />} label="" /></mode>
            
            
            
            <info>
            <h2>
              <p>Hi, i'm Sharan.</p>
              <p>I'am a web developer!</p>
              <button>Contact me</button>
            </h2>
            </info>
            

          
        </Container>
        
    )
}

export default Skills
const Container = styled.div`

background: black;
flex: 0.5;
::root{
cursor: none;
    --cursorX: 50vw;
    --cursorY: 50vh;
}


height: 90.5vh;

color: white;
h1{
    color: white;
}

ul{
  
  width: 100%;
  margin-top: 0%;
  
  
    list-style: none;
    li{
      cursor: pointer;
     color: white;
    }
}
mode{
  color: #cfd0d1;
display: block;
width: 100%;
height: 100%;
position: absolute;
top: 0;

cursor: default;
background: radial-gradient(
    circle 10vmax at var(--cursorX) var(--cursorY),
    rgba(0,0,0,0) 0%,
    rgba(0,0,0,.5) 80%,
    rgba(0,0,0,.95) 100%
);
.switch{
  position: absolute;
  right: 0;
  bottom: 0;
}
}
p{
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
info{
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;

  justify-content: center;
  
}
`;